.Frame-MainPage {
    height: 100%;
    background-color: antiquewhite;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* flex-wrap: wrap; */
    justify-content: space-around;
}

.ButtonActive-MainPage {
    display: flex;
    height: 200px;
    width: 40%;
    background-color: rgb(211, 201, 182);
    margin-top: 2%;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    font-size: xx-large;
    margin-bottom: 20px;
}

.ButtonDisabled-MainPage {
    display: flex;
    height: 100px;
    width: 40%;
    background-color: rgb(203, 201, 197);
    margin-top: 5%;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    color: #959595;
    font-size: xx-large;
}